<template>
    <div ref="canvas" />
</template>

<script lang="ts">
/* eslint-disable */
import { defineComponent, ref, onMounted } from 'vue';
import {
    Viewer,
    Cartesian3,
    UrlTemplateImageryProvider,
    Cesium3DTileset,
    HeadingPitchRange,
    Cesium3DTileStyle,
    GeoJsonDataSource,
    Color,
    Property,
    Entity,
    PolygonHierarchy,
} from 'cesium';
import 'cesium/Build/Cesium/Widgets/widgets.css';
import createWorldTerrain from 'cesium/Source/Core/createWorldTerrain';

import GsiTerrainProvider from './GsiTerrainProvider';

export default defineComponent({
    name: 'HelloWorld',
    props: {
        msg: String,
    },
    setup() {
        const canvas = ref<HTMLDivElement>();
        onMounted(async () => {
            const positron = new UrlTemplateImageryProvider({
                url:
                    'https://cyberjapandata.gsi.go.jp/xyz/seamlessphoto/{z}/{x}/{y}.jpg',
                //'https://cyberjapandata.gsi.go.jp/xyz/seamlessphoto/{z}/{x}/{y}.jpg',
            });
            const DISABLE_DECOLATIONS: Viewer.ConstructorOptions = {
                animation: false,
                baseLayerPicker: false,
                fullscreenButton: false,
                homeButton: false,
                geocoder: false,
                infoBox: false,
                timeline: false,
                navigationHelpButton: false,
                navigationInstructionsInitiallyVisible: false,
                sceneModePicker: false,
            };
            const viewer = new Viewer(canvas.value!, {
                imageryProvider: positron,
                ...DISABLE_DECOLATIONS,
                // @ts-ignore
                terrainProvider: new GsiTerrainProvider({}),
                targetFrameRate: 30,
            });
            viewer.cesiumWidget.creditContainer.remove();
            viewer.camera.setView({
                destination: Cartesian3.fromDegrees(138, 30, 3000000),
                orientation: {
                    heading: 0, // 水平方向の回転度（ラジアン）
                    pitch: -1.3, // 垂直方向の回転度（ラジアン） 上を見上げたり下を見下ろしたり
                    roll: 0,
                },
            });

            viewer.entities.add({
                name: 'test',
                polygon: {
                    hierarchy: new PolygonHierarchy(
                        Cartesian3.fromDegreesArray([
                            140.0,
                            40.0,
                            140.0,
                            42.0,
                            142.0,
                            42.0,
                            142.0,
                            40.0,
                            140.0,
                            40.0,
                        ]),
                    ),
                    height: 1000,
                    extrudedHeight: 333.0,

                    material: Color.RED.withAlpha(0.5),
                    outline: true,
                    outlineColor: Color.BLACK,
                },
            });
        });
        const test = 0;
        return {
            canvas,
            test,
        };
    },
});
</script>

